import React from "react";
import Card from "../Card/Card";
import importAllImagesText from '../Helpers/ImportAllImagesText';

const restaurantImages = await importAllImagesText(require.context('../PartnersLogos/Restaurants', false, /\.(webp)$/));

const Restaurants = () => {
    return (
        <>
            <h2>Restaurants</h2>
            {!restaurantImages.length && <h4 style={{textAlign:'center'}}>No Partners Yet!</h4>}
            <div className="section-container">
                {restaurantImages.length > 0 &&  restaurantImages.map((restImage, index) => (
                    <Card
                        classAdded={"card-for-all"}
                        key={index}
                        image={restImage.image}
                        partner_name={restImage.name}
                    />

                ))}
            </div>
        </>
    );
}

export default Restaurants;